    ...mapMutations(['showDialog']),
    executeJob(api) {
      this.$http.post('api/run/' + api)
          .then((response) => {
            console.log(response);
            this.getHistoryList();
            this.showDialog('작업 완료', '작업 완료 되었습니다.');
